<template>
    <div>
        <div class="col col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
            <div class="ui-block">
                <div class="ui-block-content">
                    <router-link to="/" style="color:#00AEEF" class="title">Back</router-link>
                </div>
            </div>
        </div>

        <div class="col col-xl-12 order-xl-2 col-lg-12 order-lg-1 col-md-12 col-sm-12 col-12" ref="deal_page">

            <div id="search-items-grid">

                <div class="ui-block">

                    <!-- Search Result -->

                    <article class="hentry post searches-item deals">

                        <div class="post__author author vcard inline-items">
                            <img :src="Deal.business_logo === null ? '' : Deal.business_logo " alt="author">

                            <div class="author-date">
                                <router-link :to=" '/profile/' + Deal.business_username " style="text-transform: capitalize"> {{ Deal.business_name === null ? Deal.business_username : Deal.business_name }} </router-link>
                                <div class="country"> {{ Deal.fixture_home_team }} (VS) {{ Deal.fixture_away_team }} </div>
                            </div>

                            <span class="notification-icon">
								<img :src="Deal.fixture_home_logo">
                                <span style="position: relative;top:-16px;right: 7px"> VS </span>
								<img :src="Deal.fixture_away_logo">
							</span>

                        </div>

                        <p class="user-description">
                            <span class="title" style="text-transform: capitalize">{{ Deal.offer_name }}</span>
                            <br>
                            <span>{{ Deal.offer_description }}</span>
                        </p>

                        <div class="post-block-photo js-zoom-gallery" v-if="Deal.offer_file.length > 0">
                            <a href="img/post-photo3.jpg" class="col col-12-width">
                                <img :src="Deal.offer_file" alt="photo">
                            </a>
                        </div>

                        <div class="post-additional-info">
                            <div class="friend-count">
                                <a href="#" class="friend-count-item">
                                    <div class="h6">189</div>
                                    <div class="title">Friends</div>
                                </a>
                                <a href="#" class="friend-count-item">
                                    <div class="h6">254</div>
                                    <div class="title">Photos</div>
                                </a>
                                <a href="#" class="friend-count-item">
                                    <div class="h6">16</div>
                                    <div class="title">Videos</div>
                                </a>
                            </div>

                        </div>
                    </article>
                    <!-- ... end Search Result -->
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "deal_page.vue",
        data() {
          return {
              Deal: {},
          }
        },
        methods: {
            showLoading() {
                let loader = this.$loader(this, this.$refs.deal_page, {
                    loaderType: "spinner"
                });
                return loader;
            },
            getSelectedDeal(id) {
                // let loader = this.showLoading();
                this.axios.get('deals/info/' + id)
                    .then(response => {
                        // loader.hide();
                        // console.log(response.data.data.offer);
                        this.Deal = response.data.data.offer;
                    })
                    .catch(error => {
                        // console.log(error);
                        // loader.hide();
                        return error;
                    })
            }
        },
        mounted() {
            let dealId = this.$route.params.deal_id;
            this.getSelectedDeal(dealId);
        }
    }
</script>

<style scoped>

</style>